html {
  background: #1f3158;
}

svg {
  display: block;
  margin: auto;
}

body {
  font-family: "Open Sans", sans-serif;
  color: white;
}
.wrapper {
  margin-left: auto;
  margin-right: auto;
  width: 70%;
  padding-right: 10px;
  padding-left: 10px;
}
input {
  width: 150px;
  margin: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
input[type="text"] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
}
input[type="button"] {
  background-color: #c6efff;
  border: none;
  color: black;
  padding: 16px 32px;
  text-decoration: none;
  font-weight: bold;
  margin: 4px 2px;
  cursor: pointer;
}
input[type="text"]:focus {
  background-color: lightblue;
}
input[type="button"]:hover {
  background-color: #e3f6fd;
}
#messages {
  margin-top: 12px;
  margin-bottom: 12px;
  padding: 12px;
  width: 100%;
  display: inline-block;
  border: 1px solid black;
  max-height: 250px;
  min-height: 250px;
  overflow: scroll;
  color: white;
  background: #111827;
}

#messages p {
  margin: 0;
}

#messages span {
  overflow-y: scroll;
  overflow: scroll;
}
